import isestr from './isestr.mjs'
import ispint from './ispint.mjs'


/**
 * n
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/strright.test.js Github}
 * @memberOf wsemi
 * @param {String} c 
 * @param {Integer} n n
 * @returns {String} 
 * @example
 * strright('test', 2)
 * // => ''
 */
function strright(c, n) {

    //check
    if (!isestr(c)) {
        return ''
    }
    if (!ispint(n)) {
        return ''
    }

    let i = c.length - n
    if (i < 0) {
        i = 0
    }

    return c.substr(i, n)
}


export default strright
