import isint from './isint.mjs'
import cint from './cint.mjs'


/**
 * 
 * 00
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/ispint.test.js Github}
 * @memberOf wsemi
 * @param {*} v 
 * @returns {Boolean} 
 * @example
 * ispint(0)
 * // => false
 *
 * ispint(125)
 * // => true
 *
 * ispint('125')
 * // => true
 *
 * ispint(1.25)
 * // => false
 */
function ispint(v) {

    //check
    if (!isint(v)) {
        return false
    }

    let r = cint(v) > 0

    return r
}


export default ispint
