import isestr from './isestr.mjs'
import ispint from './ispint.mjs'


/**
 * n
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/strleft.test.js Github}
 * @memberOf wsemi
 * @param {String} c 
 * @param {Integer} n n
 * @returns {String} 
 * @example
 * strleft('test', 2)
 * // => 'te'
 */
function strleft(c, n) {

    //check
    if (!isestr(c)) {
        return ''
    }
    if (!ispint(n)) {
        return ''
    }

    return c.substr(0, n)
}


export default strleft
