/**
 * 
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/isnbr.test.js Github}
 * @memberOf wsemi
 * @param {*} v 
 * @returns {Boolean} 
 * @example
 * isnbr(1.25)
 * // => true
 *
 * isnbr('1.25')
 * // => false
 */
function isnbr(v) {

    let c = Object.prototype.toString.call(v)
    return c === '[object Number]'
}


export default isnbr
