import isstr from './isstr.mjs'


/**
 * 
 *
 * Unit Test: {@link https://github.com/yuda-lyu/wsemi/blob/master/test/isestr.test.js Github}
 * @memberOf wsemi
 * @param {*} v 
 * @returns {Boolean} 
 * @example
 * isestr('1.25')
 * // => true
 *
 * isestr(125)
 * // => false
 *
 * isestr('')
 * // => false
 */
function isestr(v) {

    //check
    if (isstr(v)) {
        if (v !== '') {
            return true
        }
    }
    return false
}


export default isestr
